<template>
    <section class="cars">
        <Header></Header>
        <h2>Cars</h2>
        <div v-for="auto in cars">
            <router-link :to="{ name: 'car', params: { slug: auto.slug}}">{{ auto.title }}</router-link>

        </div>
        <Footer></Footer>
    </section>
</template>
<script>
    import Header from './Header.vue'
    import Footer from './Footer.vue'
    // let cars = require('../data');   for data.js
    export default {
        name: 'Cars',
        data: function(){
            return{
                cars: []
                // cars: cars   for data.js
                // cars: [
                //     {slug:'car1',title:'Car1',text:'My First Car'},
                //     {slug:'car2',title:'Car2',text:'My Second Car'},
                //     {slug:'car3',title:'Car3',text:'My Third Car'},
                //     {slug:'car4',title:'Car4',text:'My Fourth Car'},
                // ]
            }
        },
        components: {
            Header,
            Footer,
        },
        created: function () {
            let obj = this;
            this.$db.collection("cars").get().then(function(query) {
                query.forEach(function(car) {
                    // doc.data() is never undefined for query doc snapshots
                    // console.log(car.id, " => ", car.data());
                    obj.cars.push(car.data());
                    // console.log(this.cars);
                });
            });
        }
    }
</script>
